.ks_home_carousel {
	@include tab-min {
		aspect-ratio: 1920 / 607;
	}
	@include mob-only {
		aspect-ratio: 400 / 520;
		img {
			aspect-ratio: 400 / 174;
			object-fit: cover;
		}
	}
	.background_image {
		@include tab-min {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			z-index: -1;
		}
	}
	.carousel-cell {
		width: 100%;
		height: 100%;
	}
	.ks_hc_inner {
		position: relative;
		background-color: var(--color-1);
		padding: 34px;
		border-top: none;
		@include tab-min {
			max-width: 400px;
			left: 100px;
			border: 2px solid #FFF;

		}
		@include mob-only {
			width: 100%;
			aspect-ratio: 400 / 345;
			top: -6px;
			display: flex;
			flex-direction: column;
			.btn {
				margin-top: auto;
				margin-bottom: 0;
				width: fit-content;
			}
		}
		h3 {
			color: #fff;
			font-size: 30px;
			line-height: 39px;
			font-weight: 700;
			@include mob-only {
				font-size: 24px;
				line-height: 32px;
			}
			&::after {
				content: "";
				background-color: var(--color-2);
				width: 84px;
				height: 7px;
				display: block;
				margin-block: 23px 13px;
			}
		}
		div {
			color: #fff;
			margin-bottom: var(--space-3);
			p {
				font-size: 16px;
				line-height: 22px;
				font-weight: 600;
			}
		}
	}
}

.ks_home_sec1 {
	.ks_hs1_inner {
		display: flex;
		flex-direction: column;
		align-items: center;
		h2 {
			position: relative;
			&::after {
				content: "";
				display: block;
				position: relative;
				width: 142px;
				height: 7px;
				margin-top: 18px;
				margin-bottom: 26px;
				background-color: var(--color-2);
				left: 50%;
				transform: translateX(-50%);
			}
		}
		.sec1_text {
			text-align: center;
			max-width: 757px;
			margin-bottom: var(--space-2);
			p {
				font-weight: 400;
			}
		}
		.sec1_images {
			width: 100%;
			max-width: 600px;
			flex-wrap: wrap;
			display: flex;
			justify-content: space-between;
			align-items: center;
			@include tab-max {
				img {
					margin-inline: auto;
				}
			}
		}
	}
}

.ks_home_sec2 {
	position: relative;
	.ks_hs2_inner {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		.info_box {
			@include limbo-min {
				width: 31%;
			}
			@include tab-only {
				width: 48%;
			}
			@include mob-only {
				width: 100%;
			}
		}
	}
}

.cta_track {
	background-color: rgba(44,41,37,0.93);
	.container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include tab-max {
			flex-direction: column;
			h3 {
				margin-bottom: var(--space-4);
			}
		}
	}
	h3 {
		color: #fff;
		padding-bottom: 0;
		span {
			color: var(--color-2)
		}
	}
}


.ks_home_sec3 {
	background-color: #D7D7D7;
	.container {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		.ks_col {
			@include limbo-min {
				width: 48%;
			}
			margin-bottom: 4%;
		}
	}
	.btn {
		left: 50%;
		position: relative;
		transform: translateX(-50%);
	}
}
